import React, { useEffect, useState } from "react";
import { Form, Card, Input, Button, message, Upload, Icon } from "antd";
import { createApi, getOneById, modifyOne } from "../../../services/products";
import create from "antd/lib/icon/IconFont";
import { serverUrl, serverImageUrl } from '../../../utils/config';
import {getToken} from '../../../utils/auth';

// 引入编辑器组件
import BraftEditor from 'braft-editor'
// 引入编辑器样式
import 'braft-editor/dist/index.css'

function Edit(props) {
  console.log(props);
  // props.match.params.id 存在的话表示修改，否则为新增
  const { getFieldDecorator } = props.form;
  const [currentData, setCurrentData] = useState([]);
  const [imageUrl, setImageUrl] = useState("");
  const [loading, setLoading] = useState(false);
  const [editorState, setEditorState] = useState(BraftEditor.createEditorState(''));

  useEffect(() => {
    if (props.match.params.id) {
      getOneById(props.match.params.id)
        .then((res) => {
          console.log(res);
          setCurrentData(res.data);
          setImageUrl(res.data.coverImg);
          setEditorState(BraftEditor.createEditorState(res.data.content));
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, []);

  const uploadButton = (
    <div>
      <Icon type={loading ? "loading" : "plus"} />
      <div className="ant-upload-text">Upload</div>
    </div>
  );

  // 图片上传
  const handleChange = (info) => {
      // 上传中
    if (info.file.status === "uploading") {
      setLoading(true);
      return;
    }
    // 上传成功
    if (info.file.status === "done") {
      setLoading(false);
      console.log("upload sucess:" + info);
      setImageUrl(info.file.response.data);
    }
  };

// 富文本编辑器
const handleEditorChange = (v) => {
  setEditorState(v);
}


  const priceValidate = (rule, value, callback) => {
    console.log("value:" + value);
    if (value * 1 > 100) {
      callback("价格不能大于100");
    } else {
      callback();
    }
  };

  const handleSubmit = (e) => {
    console.log(e);
    // 获取当前富文本的内容
    e.preventDefault();
    
    props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log("提交");
        // 此处需要调用 api 接口
        if (props.match.params.id) {
          modifyOne(props.match.params.id, {...values, coverImg: imageUrl, content: editorState.toHTML()})
            .then((res) => {
              console.log(res);
              if (200 === res.code) {
                message.info(res.message);
                props.history.push("/admin/products");
              } else {
                message.error(res.message);
              }
            })
            .catch((err) => {
              console.log(err);
            });
        } else {
          createApi({...values, coverImg: imageUrl, content: editorState.toHTML()})
            .then((res) => {
              console.log(res);
              if (200 === res.code) {
                message.info(res.message);
                props.history.push("/admin/products");
              } else {
                message.error(res.message);
              }
            })
            .catch((err) => {
              console.log(err);
            });
        }
      }
    });
  };

  return (
    <Card
      title="商品编辑"
      extra={
        <Button
          type="default"
          size="small"
          onClick={() => props.history.push("/admin/products")}
        >
          返回
        </Button>
      }
    >
      <Form onSubmit={(e) => handleSubmit(e)}>
        <Form.Item label="名字">
          {getFieldDecorator("name", {
            rules: [
              {
                required: true,
                message: "请输入商品名字",
              },
            ],
            initialValue: currentData.name,
          })(<Input placeholder="请输入商品名字"></Input>)}
        </Form.Item>
        <Form.Item label="价格">
          {getFieldDecorator("price", {
            rules: [
              {
                required: true,
                message: "请输入商品价格",
              },
              {
                validator: priceValidate,
              },
            ],
            initialValue: currentData.price,
          })(<Input placeholder="请输入商品价格"></Input>)}
        </Form.Item>
        <Form.Item label="主图">
          <Upload
            name="file"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}
            action={serverUrl+'/api/v1/admin/products/file_upload'}
            onChange={(info) => handleChange(info)}
            headers={{
                authorization: 'Bearer ' + getToken(), 
            }}
          >
            {imageUrl ? (
              <img src={serverImageUrl + imageUrl} alt="avatar" style={{ width: "100%" }} />
            ) : (
              uploadButton
            )}
          </Upload>
        </Form.Item>
        <Form.Item label="详情">
        <BraftEditor
                    value={editorState}
                    onChange={(e)=>handleEditorChange(e)}
                />
        </Form.Item>
        <Form.Item>
          <Button htmlType="submit" type="primary">
            保存
          </Button>
        </Form.Item>
      </Form>
    </Card>
  );
}

export default Form.create({ name: "productEdit" })(Edit);
