import { post, get, put, del } from '../utils/request';

/**
 * 获取列表
 * @param {*} page 
 */
export function listApi(pageNum=1,pageSize=2) {
    return get('/api/v1/admin/products', {pageNum, pageSize});
}

/**
 * 创建数据
 * @param {*} data 
 */
export function createApi(data) {
    return post('/api/v1/admin/products', data);
}

/**
 * 根据id获取商品
 * @param {*} id 
 */
export function getOneById(id) {
    return get(`/api/v1/admin/products/${id}`);
}

/**
 * 修改数据
 * @param {*} id 
 * @param {*} data 
 */
export function modifyOne(id, data) {
    return put(`/api/v1/admin/products/${id}`, data); 
}

/**
 * 删除数据
 * @param {*} id 
 * @param {*} data 
 */
export function delOne(id, data) {
    return del(`/api/v1/admin/products/${id}`, data); 
}