import { listApi } from "../../services/products";

export const loadProduct = payload => async dispatch => {
    console.log(payload);
    const res = await listApi(payload.page, payload.size);
    // 当异步操作完成之后通过 dispatch 触发 reducer 改变数据
    dispatch({
        type: 'PRODUCT_LOADED',
        payload: res
    })
}