import React from "react";
import {
  Layout,
  Menu,
  Breadcrumb,
  Icon,
  Dropdown,
  Avatar,
  message,
  Badge,
} from "antd";
import { adminRoutes } from "../../routes";
import { withRouter } from "react-router-dom";
import "./frame.css";
import { clearToken } from "../../utils/auth";
import { connect } from "react-redux";

const { SubMenu } = Menu;
const { Header, Content, Footer, Sider } = Layout;

const routes = adminRoutes.filter((route) => route.isShow);

function Index(props) {
  console.log(props);
  const popMenu = (
    <Menu
      onClick={(p) => {
        if (p.key == "logOut") {
          clearToken();
          props.history.push("/login");
        } else {
          //message.info(p.key); // tip
          if (p.key == "noti") {
            props.history.push("/admin/notices");
          }
        }
      }}
    >
      <Menu.Item key="noti">通知中心</Menu.Item>
      <Menu.Item key="setting">设置</Menu.Item>
      <Menu.Item key="logOut">退出</Menu.Item>
    </Menu>
  );

  return (
    <Layout>
      <Header className="header" style={{ backgroundColor: "#428bca" }}>
        <div className="logo">{/* <img src={logo} alt="logo"/> */}</div>
        {/* <Menu
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={['2']}
            style={{ lineHeight: '64px' }}
          >
            <Menu.Item key="1">nav 1</Menu.Item>
            <Menu.Item key="2">nav 2</Menu.Item>
            <Menu.Item key="3">nav 3</Menu.Item>
          </Menu> */}

        <Dropdown overlay={popMenu}>
          <div>
            <Avatar>U</Avatar>

            <Badge dot={!props.isAllRead}>
              {" "}
              <span style={{ color: "#fff" }}>超级管理员</span>
            </Badge>

            <Icon type="down"></Icon>
          </div>
        </Dropdown>
      </Header>
      <Content style={{ padding: "0 50px" }}>
        {/* <Breadcrumb style={{ margin: '16px 0' }}>
            <Breadcrumb.Item>Home</Breadcrumb.Item>
            <Breadcrumb.Item>List</Breadcrumb.Item>
            <Breadcrumb.Item>App</Breadcrumb.Item>
          </Breadcrumb> */}
        <Layout style={{ padding: "24px 0", background: "#fff" }}>
          <Sider width={200} style={{ background: "#fff" }}>
            <Menu
              mode="inline"
              defaultSelectedKeys={["1"]}
              defaultOpenKeys={["sub1"]}
              style={{ height: "100%" }}
            >
              {routes.map((route) => {
                return (
                  <Menu.Item
                    key={route.path}
                    onClick={(p) => props.history.push(p.key)}
                  >
                    <Icon type={route.icon} />
                    {route.title}
                  </Menu.Item>
                );
              })}
            </Menu>
          </Sider>
          <Content style={{ margin: 0, minHeight: 280 }}>
            {props.children}
          </Content>
        </Layout>
      </Content>
      <Footer style={{ textAlign: "center" }}>
        Ant Design ©2018 Created by Ant UED
      </Footer>
    </Layout>
  );
}

const mapStateToProps = state=>state.notice;

// 这里的 state 相当于定了一个上面的方法
export default connect(mapStateToProps)(withRouter(Index));
