function List(props) {
  // 定义局部状态
  // const [dataSource, setDataSource] = useState([]);
  // const [total, setTotal] = useState([]);
  // const [currentPage, setCurrentPage] = useState([1]);

  const { list, page, total } = props;


  useEffect(() => {


    props.dispatch(
      loadProduct({page: 1, size: 5})
    );

    // listApi().then((res) => {
    //   console.log(res);
    //   setDataSource(res.data.list);
    //   setTotal(res.data.total);
    //   setCurrentPage(res.current);
    // });
  }, []);

  const loadData = (current, size) => {
    console.log("page:" + current + ", pageSize:" + size);

    props.dispatch(
      loadProduct({page: current, size: size})
    );


    // listApi(current, size)
    //   .then((res) => {
    //     if (res.code === 200) {
    //       console.log(res);
    //       setDataSource(res.data.list);
    //       setTotal(res.data.total);
    //     } else {
    //       message.info(res.message);
    //     }
    //   })
    //   .catch((err) => {
    //     console.log("Error:" + err);
    //     message.error("" + err);
    //   });
  };

  const loadData1 = (page, pageSize) => {
    console.log("page:" + page + ", pageSize:" + pageSize);

    props.dispatch(
      loadProduct({page: page, size: pageSize})
    );


    // listApi(page, pageSize)
    //   .then((res) => {
    //     if (res.code === 200) {
    //       console.log(res);
    //       setDataSource(res.data.list);
    //       setTotal(res.data.total);
    //     } else {
    //       message.info(res.message);
    //     }
    //   })
    //   .catch((err) => {
    //     console.log("Error:" + err);
    //     message.error("" + err);
    //   });
  };

  const columns = [
    {
      title: "序号",
      key: "id",
      width: 80,
      align: "center",
      render: (txt, record, index) => index + 1,
    },
    {
      title: "名字",
      dataIndex: "name",
    },
    {
      title: "主图",
      dataIndex: "coverImg",
      render: (txt, record, index) => record.coverImg? (<img src={serverImageUrl + record.coverImg} alt={record.name} style={{width: "120px"}} />) : ('暂无图片') 
    },
    {
      title: "价格",
      dataIndex: "price",
    },
    {
      title: "是否在售",
      dataIndex: "onSale",
      render: (txt, record) => (record.onSale ? "在售" : "已下架")
    },
    {
      title: "操作",
      render: (txt, record, index) => {
        return (
          <div>
            <Button
              type="primary"
              size="small"
              onClick={() =>
                // 跳转到 edit 页面，传递 id 作为参数
                props.history.push(`/admin/products/edit/${record.id}`)
              }
            >
              修改
            </Button>
            <Popconfirm
              title="确定删除此项？"
              onCancel={() => console.log("用户取消删除")}
              onConfirm={() => {
                // 此处调用 api 接口进行相关操作
                delOne(record.id).then(
                  res=>{
                    loadData(page);
                  }
                )
              }}
            >
              <Button style={{ margin: "0 1rem" }} type="danger" size="small">
                删除
              </Button>
            </Popconfirm>
            <Button size="small" onClick={()=>{
              modifyOne(record.id, {name: record.name, price: record.price, onSale: record.onSale ? 0 : 1}).then((res)=>{
                loadData(page);
              });
            }}>{record.onSale? "下架":"上架"}</Button>
          </div>
        );
      },
    },
  ];

  return (
    <Card
      title="商品列表"
      extra={
        <Button
          type="primary"
          size="small"
          onClick={() => props.history.push("/admin/products/edit")}
        >
          新增
        </Button>
      }
    >
      <Table
        rowKey="id"
        rowClassName={record => (record.onSale? "":"bg-red")}
        pagination={{
          defaultCurrent: 1,
          total,
          defaultPageSize: 2,
          showSizeChanger: true,
          onShowSizeChange: loadData,
          onChange: loadData1,
        }}
        columns={columns}
        bordered
        dataSource={list}
      />
    </Card>
  );
}

export default connect(state=>state.product)(List);
